$contact: #FF1E00;
#container {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    #background_video {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        object-fit: cover;
        height: 100%;
        width: 100%; }
    #video_cover {
        position: absolute;
        width: 100%;
        height: 100%;
        background: url('../Images/video_cover.jpg') no-repeat;
        background-size: cover;
        background-position: center;
        @include pad {
            background: url('../Images/M1_video_cover.jpg') no-repeat;
            background-size: cover;
            background-position: center; } } }
#container nav .contactColor {
    color: $contact !important;
    @include pad {
        background: linear-gradient(to right, #F20B17, #F44846);
        -webkit-background-clip: text;
        color: transparent !important;
        background-clip: text; } }
.IG-facebook-Info {
    width: 360px;
    height: 80px;
    position: absolute;
    z-index: 20;
    font-size: 24px;
    font-family: 'Noto Sans';
    background: linear-gradient(to right, #D81109, #F93629);
    -webkit-background-clip: text;
    color: transparent;
    background-clip: text;
    text-decoration: none;
    border: 2px solid #f54d48;
    border-radius: 10px;
    top: 440px;
    &:hover {
        text-decoration: none;
        color: #f54d48; }
    @include mediumdesktop {
        width: 300px;
        font-size: 20px; }
    @include pad {
        top: unset;
        bottom: 14%; }
    @include iphone {
        width: unset;
        height: unset;
        border: none;
        font-size: 60px;
        color: #F44846;
        padding: 0;
        background: unset; }
    span {
        @include iphone {
            display: none; } }
    i {
        display: none;
        @include iphone {
            display: inline; } } }
.infoLeft {
    left: 5%;
    line-height: 80px;
    text-align: center;
    @include largedesktop {
        left: 2.5%; }
    @include iphone {
        line-height: 1;
        left: 100px; }
    @include i5 {
        left: 70px; } }
.infoRight {
    right: 5%;
    text-align: left;
    line-height: 1.5;
    padding: 0 15px;
    @include mediumdesktop {
        right: 2.5%;
        line-height: 1.8; }
    @include iphone {
        right: 100px;
        line-height: 1; }
    @include i5 {
        right: 70px; } }
#container {
    #overlay {
        .mobileMenu {
            display: none;
            width: 37px;
            height: 31px;
            position: fixed;
            bottom: 10px;
            left: 10px;
            text-align: center;
            line-height: 0.8;
            transition: all .3s;
            @include pad {
                display: inline-block; }
            @include i5 {
                line-height: 0.4;
                bottom: 5px;
                left: 5px; }
            > * {
                display: inline-block;
                width: 40px;
                height: 5px;
                background-color: #FFffff;
                opacity: 0.7;
                transition: transform .3s;
                @include i5 {
                    width: 20px;
                    height: 4px; } }
            .topLine {
                transform: translatey(7px) rotate(45deg); }
            .bottomLine {
                transform: translatey(-5px) rotate(135deg);
                @include i5 {
                    transform: translatey(-1px) rotate(135deg); } }
            .contactSpan {
                background-color: #FF1E00; } }
        .menuClick {
            bottom: 110px;
            right: 10px;
            @include i5 {
                bottom: 50px; } }
        .contactNav {
            position: fixed;
            bottom: 7.5%;
            left: 0;
            right: 0;
            z-index: 100;
            width: 60%;
            margin: 0 auto;
            @include mediumdesktop {
                width: 80%; }
            @include pad {
                width: 100%;
                height: 0px;
                background-color: rgba(255, 30, 0, 0.3);
                bottom: -5%;
                transition: height .3s; }
            a {
                color: white;
                text-align: center;
                font-size: 26px;
                font-family: "Orator Std";
                line-height: 1.5;
                @include i5 {
                    font-size: 14px !important; } } }
        .menu-open {
            height: 100px;
            bottom: 0%;
            @include i5 {
                height: 50px; } } } }
