.wrap {
    width: 100%;
    height: 100%;
    background-image: url(../Images/project＿BG.jpg);
    background-position: center;
    background-size: cover;
    @include pad {
        background-image: url(../Images/M_project.jpg); }
    .sideImg {
        position: absolute;
        top: 10%;
        right: 8.3%;
        width: 6%;
        @include largestdesktop {
            right: 1%; }
        @include mediumdesktop {
            top: 13%;
            right: 4.3%; }
        @include desktop {
            top: 13%;
            right: 2%; }
        @include pad {
            display: none; } }
    .video-container {
        padding-top: 80px;
        text-align: center;
        @include pad {
            position: relative;
            padding-bottom: 56.25%;
            padding-top: 30px;
            height: 0;
            top: 10%;
            overflow: hidden; }
        iframe {
            @include mediumdesktop {
                width: 1200px;
                height: 632px; }
            @include desktop {
                width: 900px;
                height: 474px; } } }
    .video-container iframe, .video-container object, .video-container embed {
            @include pad {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%; } }
    .mobileMenu {
        display: none;
        width: 37px;
        height: 31px;
        position: fixed;
        bottom: 10px;
        left: 10px;
        text-align: center;
        line-height: 0.8;
        transition: all .3s;
        @include pad {
            display: inline-block;
            @include i5 {
                line-height: 0.4;
                bottom: 5px;
                left: 5px; } }
        > * {
            display: inline-block;
            width: 40px;
            height: 5px;
            background-color: #F4EC35;
            opacity: 0.7;
            transition: transform .3s;
            @include i5 {
                width: 20px;
                height: 4px; } }
        .topLine {
            transform: translatey(7px) rotate(45deg); }
        .bottomLine {
            transform: translatey(-5px) rotate(135deg);
            @include i5 {
                transform: translatey(-1px) rotate(135deg); } } }
    .menuClick {
        bottom: 110px;
        right: 10px;
        @include i5 {
            bottom: 50px; } }
    nav {
        position: fixed;
        bottom: 7.5%;
        left: 0;
        right: 0;
        z-index: 100;
        width: 60%;
        margin: 0 auto;
        @include mediumdesktop {
            width: 80%; }
        @include pad {
            width: 100%;
            height: 0px;
            background-color: rgba(0, 0, 0, 0.4);
            bottom: -5%;
            transition: height .3s; }
        a {
            color: white;
            text-align: center;
            font-size: 26px;
            font-family: "Orator Std";
            @include i5 {
                font-size: 14px; } } }
    .menu-open {
        height: 100px;
        bottom: 0%;
        @include i5 {
            height: 50px; } }
    .projectColor {
        color: #F4EC35; } }
.cover {
    opacity: 0.5;
    background-color: white; }
.modalBomb {
    width: 655px;
    height: 655px;
    background-color: #576537;
    position: absolute;
    top: 11%;
    left: 0;
    right: 0;
    margin: 0 auto;
    box-shadow: 10px 10px #656868;
    @include mediumdesktop {
        width: 555px;
        height: 555px; }
    @include i5 {
        width: 300px;
        height: 425px;
        box-shadow: 5px 5px #656868; }
    .closeBtn {
        position: absolute;
        right: 10px;
        top: 0px;
        font-size: 40px;
        color: white;
        display: inline-block;
        background-color: #576537;
        cursor: pointer; }
    .modalContent {
        width: 93%;
        height: 97%;
        border-right: 1px solid #3E5103;
        border-bottom: 1px solid #3E5103;
        margin: 0 auto;
        font-size: 26px;
        font-family: 'Noto Sans';
        color: white;
        @include i5 {
            font-size: 20px; }
        h2 {
            font-weight: 500;
            padding: 50px 0 10px 0;
            @include mediumdesktop {
                padding: 30px 0 10px 0; }
            @include i5 {
                padding: 15px 0 5px 0; } }
        .enTitle {
            padding: 0px 0 50px 0;
            color: #F4EC35;
            @include mediumdesktop {
                padding: 0px 0 30px 0; }
            @include i5 {
                padding: 0px 0 10px 0; } }
        p {
            font-size: 20px;
            line-height: 1.4;
            font-weight: 300;
            padding-bottom: 30px;
            @include i5 {
                font-size: 16px;
                padding-bottom: 15px; } } } }
