$contact: #9C5618;
.wrap {
    .aboutWrap {
        display: flex;
        width: 100%;
        height: 100%;
        background-image: url(../Images/about_BG.jpg);
        background-position: center;
        background-size: cover;
        .aboutInfo {
            width: 70%;
            border-top: 1px solid $contact;
            height: calc(100% - 20%);
            margin: 0 auto;
            position: relative;
            @include i5 {
                border-top: none; }
            .aboutImg {
                height: 30%;
                position: absolute;
                width: 1px;
                right: 0;
                border-right: 1px solid #9C5618;
                @include i5 {
                    display: none; }
                img {
                    width: 75px;
                    height: 279px;
                    position: absolute;
                    right: -40px; } }
            .aboutFontSetting {
                font-size: 20px;
                color: $contact;
                font-family: 'Noto Sans';
                padding-top: 40px;
                line-height: 1.4;
                font-weight: 300;
                @include mediumdesktop {
                    font-size: 18px;
                    padding-top: 30px; }
                @include desktop {
                    font-size: 16px; }
                @include i5 {
                    font-size: 14px;
                    padding-top: 20px; } } } }
    .mobileMenu {
        display: none;
        width: 37px;
        height: 31px;
        position: fixed;
        bottom: 10px;
        left: 10px;
        text-align: center;
        line-height: 0.8;
        transition: all .3s;
        @include pad {
            display: inline-block;
            @include i5 {
                line-height: 0.4;
                bottom: 5px;
                left: 5px; } }
        > * {
            display: inline-block;
            width: 40px;
            height: 5px;
            background-color: #9C5618;
            opacity: 0.7;
            transition: transform .3s;
            @include i5 {
                width: 20px;
                height: 4px; } }
        .mobileSpan {
            background-color: #9C5618; }
        .topLine {
            transform: translatey(7px) rotate(45deg); }
        .bottomLine {
            transform: translatey(-5px) rotate(135deg);
            @include i5 {
                transform: translatey(-1px) rotate(135deg); } } }
    .menuClick {
        bottom: 110px;
        right: 10px;
        @include i5 {
            bottom: 50px; } }
    nav {
        position: fixed;
        bottom: 7.5%;
        left: 0;
        right: 0;
        z-index: 100;
        width: 60%;
        margin: 0 auto;
        @include mediumdesktop {
            width: 80%; }
        @include pad {
            width: 100%;
            height: 0px;
            background-color: rgba(156, 86, 24, 0.4);
            bottom: -5%;
            transition: height .3s; }
        a {
            color: white;
            text-align: center;
            font-size: 26px;
            font-family: "Orator Std";
            @include i5 {
                font-size: 14px; } } }
    .menu-open {
        height: 100px;
        bottom: 0%;
        @include i5 {
            height: 50px; } }
    .aboutColor {
        color: $contact; } }
