.mainwrap {
    #container {
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        #background_video {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
            height: 100%;
            width: 100%; }
        #video_cover {
            position: absolute;
            width: 100%;
            height: 100%;
            background: url('../Images/video_cover.jpg') no-repeat;
            background-size: cover;
            background-position: center;
            @include pad {
                background: url('../Images/M_video_cover.jpg') no-repeat;
                background-size: cover;
                background-position: center; } }
        #overlay {
            osition: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0; }
        .mobileMenu {
            display: none;
            width: 37px;
            height: 31px;
            position: fixed;
            bottom: 10px;
            left: 10px;
            text-align: center;
            line-height: 0.8;
            transition: all .3s;
            @include pad {
                display: inline-block; }
            @include i5 {
                line-height: 0.4;
                bottom: 5px;
                left: 5px; }
            > * {
                display: inline-block;
                width: 40px;
                height: 5px;
                background-color: #ffffff;
                opacity: 0.7;
                transition: transform .3s;
                @include i5 {
                    width: 20px;
                    height: 4px; } }
            .topLine {
                transform: translatey(7px) rotate(45deg); }
            .bottomLine {
                transform: translatey(-5px) rotate(135deg); } }
        .menuClick {
            bottom: 110px;
            right: 10px; }
        nav {
            position: fixed;
            bottom: 7.5%;
            left: 0;
            right: 0;
            z-index: 100;
            width: 60%;
            margin: 0 auto;
            @include mediumdesktop {
                width: 80%; }
            @include pad {
                width: 100%;
                height: 0px;
                background-color: rgba(0, 0, 0, 0.4);
                bottom: -5%;
                transition: height .3s; }
            a {
                color: white;
                text-align: center;
                font-size: 26px;
                font-family: "Orator Std";
                @include i5 {
                    font-size: 14px !important; } } }
        .menu-open {
            height: 100px;
            bottom: 0%; }
        .banner {
            .bannerBg {
                width: 50%;
                position: absolute;
                z-index: 20;
                left: 0;
                right: -50px;
                margin: 0 auto;
                @include largedesktop {
                    top: 70px; }
                @include desktop {
                    top: 40px;
                    width: 65%;
                    right: -25px; }
                @include pad {
                    width: 85%;
                    right: -25px; }
                @include i5 {
                    width: 100%;
                    right: -10px;
                    top: 40px; } }
            .BgII {
                top: 46%;
                width: 59%;
                right: 0px;
                @include mediumdesktop {
                    top: 39%; }
                @include desktop {
                    width: 65%; }
                @include pad {
                    width: 80%;
                    top: 35%; }
                @include i5 {
                    width: 91%;
                    top: 33%; } } } } }
