$team: #F4EC35;
.wrap {
    .backgroundImg {
        display: flex;
        width: 100%;
        height: 100%;
        background-image: url(../Images/team.png);
        background-position: center;
        background-size: cover;
        .insideBlock {
            background-color: black;
            margin: 0 auto;
            width: 85%;
            height: 85%;
            border: 35px solid black;
            @include i5 {
                border: 15px solid black; }
            .teamColor {
                color: $team; }
            .insidestBlock {
                margin: 0 auto;
                width: 100%;
                height: 100%;
                background-image: url(../Images/team.png);
                background-size: cover;
                border: 1px solid $team;
                position: relative;
                .bombingTitle {
                    font-size: 32px;
                    transform:rotate(90deg) {}
                    color: $team;
                    transform: rotate(270deg);
                    display: inline-block;
                    position: absolute;
                    left: -195px;
                    letter-spacing: 6px;
                    top: 125px;
                    @include i5 {
                        display: none; } }
                .teamMate {
                    text-align: right;
                    line-height: 1.7;
                    font-size: 20px;
                    color: $team;
                    padding: 30px 35px 0 0;
                    @include i5 {
                        padding: 15px 20px 0 0;
                        font-size: 12px;
                        line-height: 2.5; }
                    a {
                        color: $team;
                        .i5off {
                            @include i5 {
                                display: none; } } } } }
            .mobileMenu {
                display: none;
                width: 37px;
                height: 31px;
                position: fixed;
                bottom: 10px;
                left: 10px;
                text-align: center;
                line-height: 0.8;
                transition: all .3s;
                @include pad {
                    display: inline-block;
                    @include i5 {
                        line-height: 0.4;
                        bottom: 5px;
                        left: 5px; } }
                > * {
                    display: inline-block;
                    width: 40px;
                    height: 5px;
                    background-color: #FFFF00;
                    opacity: 0.7;
                    transition: transform .3s;
                    @include i5 {
                        width: 20px;
                        height: 4px; } }
                .topLine {
                    transform: translatey(7px) rotate(45deg); }
                .bottomLine {
                    transform: translatey(-5px) rotate(135deg);
                    @include i5 {
                        transform: translatey(-1px) rotate(135deg); } } }
            .menuClick {
                bottom: 110px;
                right: 10px;
                @include i5 {
                    bottom: 50px; } }
            nav {
                position: fixed;
                bottom: 7.5%;
                left: 0;
                right: 0;
                z-index: 100;
                width: 60%;
                margin: 0 auto;
                @include mediumdesktop {
                    width: 80%; }
                @include pad {
                    width: 100%;
                    height: 0px;
                    background-color: rgba(0, 0, 0, 0.6);
                    bottom: -5%;
                    transition: height .3s; }
                a {
                    color: white;
                    text-align: center;
                    font-size: 26px;
                    font-family: "Orator Std";
                    @include i5 {
                        font-size: 14px; } } }
            .menu-open {
                height: 100px;
                bottom: 0%;
                @include i5 {
                    height: 50px; } }
            .aboutColor {
                color: $contact; } } } }
