@mixin largestdesktop {
    @media (max-width: 1600px) {
        @content; } }
@mixin mediumdesktop {
    @media (max-width: 1440px) {
        @content; } }
@mixin largedesktop {
    @media (max-width: 1366px) {
        @content; } }
@mixin desktop {
    @media (max-width: 1024px) {
        @content; } }
@mixin pad {
    @media (max-width: 768px) {
        @content; } }
@mixin iphone {
    @media (max-width: 568px) {
        @content; } }
@mixin i5 {
    @media (max-width: 414px) {
        @content; } }
